import React, { useEffect, useState,useContext } from 'react'
import Web3 from 'web3'
import { useHistory } from 'react-router-dom'
import Loader from '../../uiElements/Loader'
import LoaderBig from '../../uiElements/LoaderBig'
import './style.css'


//components
import Header from '../../uiElements/Header'
import FAQ from '../../uiElements/FAQ'


export default function Dashboard() {

    const [formStep, setFormStep] = useState(1)
  
   


  

    //Exchange Buy Buttons Loader
    const [isLoading, setIsLoading] = useState(false)
    //Balance
    const [maticBalance, setMaticBalance] = useState(0.00)
    const [aromaBalance, setAromaBalance] = useState(0.00)
    //Exchange
    const [maticAmount, setMaticAmount] = useState(0.00)
    const [aromAmount, setAromaAmount] = useState(0.00)
    //Market Details
    const [remainingNft, setRemainingNft] = useState(0)
    const [soldNft, setSoldNft] = useState(0)
    const [season, setSeason] = useState(0)
    const [nftPrice, setNftPrice] = useState(0)


    const exchangeFunc = () => {
        setIsLoading(true)
        alert("exchangeFunc")
        //setIsLoading(false)
    }

    const buyFunc = () => {
        setIsLoading(true)
        alert("buyFunc")
        //setIsLoading(false)
    }







    return (
        <React.Fragment>

            <Header />

            <div className="pageOuter">
                <div className="pageWrapper">

                
                    <div className="dashboardTitle">
                        Store
                    </div>


               
                    <div className="market">
                    <div className="marketLeft">


                    <div className="walletBalance">
                            <div className="aromaTitle aromaTitle2">
                                        Wallet Balance
                            </div>
                            <div className="walletBalanceLine walletBalanceLine1">
                                <div className="walletBalanceLineLeft">{maticBalance}</div>
                                <div className="walletBalanceLineRight"><div className="inputTokenImgMatic inputTokenImgMatic2">
                                               
                                               </div> MATIC</div>
                            </div>
                            <div className="walletBalanceLine">
                                <div className="walletBalanceLineLeft">{aromaBalance}</div>
                                <div className="walletBalanceLineRight"><div className="inputTokenImgAroma inputTokenImgAroma2">
                                               
                                               </div>  AROMA</div>
                            </div>
                        </div>
                 
              
                    <div className="progressBar">
                        <div className="progressBarItem">
                        <div className="progressBarItemNumber">1</div>
                        Exchange AROMA tokens
                       </div>
                       <div className="progressBarItem">
                       <div className="progressBarItemNumber">2</div>
                       Buy CryptoChef
                       </div>
                       <div className="progressBarItem progressBarItemLast">
                       <div className="progressBarItemNumber">3</div>
                       Wait for NFT reveal
                       </div>
                       <div className="progress">
                        <span style={{width: `${formStep * 25}%`}} className="progress-bar"></span>
                                        </div>
                    </div>

                   

                 
                        <div className="aroma">
                                        <div className="aromaNumber">
                                            1
                                        </div>

                                        <div className="aromaTitle">
                                            AROMA Exchange
                                        </div>
                                        <div className="aromaHint">
                                            I am buying
                                        </div>
                                        <div className="aromaInputArea">
                                            <input className="aromaInput" type="text"  value={aromAmount} onChange={(e) => setAromaAmount(e.target.value)} />
                                            <div className="inputToken">
                                            <div className="inputTokenImgAroma">
                                               
                                            </div>     AROMA
                                            </div>
                                        </div>
                                      
                                        <div className="aromaArrow">
                                        <i className="fas fa-chevron-down"></i>
                                        </div>
                                        
                                        <div className="aromaHint">
                                            I am paying
                                        </div>
                                        <div className="aromaInputArea">
                                        <input className="aromaInput" type="text" value={maticAmount} onChange={(e) => setMaticAmount(e.target.value)} />
                                        <div className="inputToken"> <div className="inputTokenImgMatic">
                                               
                                               </div>  
                                                MATIC
                                            </div>
                                        </div>
                                        <div className="aromaButton" onClick={exchangeFunc}>
                                         
                                            {isLoading ? <LoaderBig /> : "Exchange"}
                                        </div>
                                        </div>
                        
                        <div className="aroma aroma2">
                        <div className="aromaNumber">
                                            2
                                        </div>
                                        <div className="aromaTitle aromaTitle3">
                                            Buy CryptoChef
                                        </div>
                                        <div className="remainingHint">
                                            Remaining CryptoChefs
                                        </div>
                                        <div className="remaining">
                                            {remainingNft}
                                        </div>
                                        <div className="remainingHint2">
                                            <span>{soldNft}</span> Sold
                                         </div>
                                         <div className="buyLine">
                                            <div className="buyLineLeft">
                                             Season {season}
                                             </div>
                                             <div className="buyLineRight">
                                             <div className="remainingHint remainingHint3">
                                            Price per CryptoChef
                                             </div>
                                             <div className="remaining remaining2">
                                            {nftPrice} AROMA
                                        </div>
                                             </div>
                                         </div>
                                         <div className="aromaButton aromaButton2" onClick={buyFunc}>
                                            
                                            {isLoading ? <LoaderBig />  : "Buy CryptoChef"}
                                        </div>
                                        </div>
                    
                    <div className="saleItem">
                    <div className="aromaNumber">
                                            3
                                        </div>
                     <div className="saleItemTitle">
                       CryptoChef
                           </div>
                           <div className="saleItemHidden">
   
                           </div>
                          </div> 
     
              
                        </div>
                       
                        
   
                          


                          
                           
                        </div>

                        <div className="dashboardTitle dashboardTitle2">
                        FAQs
                        </div>
                        <FAQ />
                   </div>
                    </div>
            
           
       
        </React.Fragment>
    )
}
