import React from 'react'
import Web3 from 'web3'
import { useState } from 'react';
import { useEffect } from 'react';
import { useHistory } from 'react-router-dom'
import './style.css'
import Loader from '../Loader'

export default function Header() {

    const history = useHistory();

    const [walletAddr, setWalletAddr] = useState(localStorage.addr)
    const [isWalletLoading, setIsWalletLoading] = useState(false)
    const [isWalletConnected, setIsWalletConnected] = useState(false)

    const [showConnectModal, setShowConnectModal] = useState(false)


    //Metamask connection func
    const loadWeb3 = () => {
        if(window.ethereum){
            window.web3 = new Web3(window.ethereum)
            window.ethereum.enable()
                .then(info => {
                    window.web3.eth.getAccounts()
                        .then(accounts => {
                            setIsWalletLoading(false)
                            setWalletAddr(accounts[0])
                            setIsWalletConnected(true)
                            setShowConnectModal(false)
                            localStorage.setItem("addr", accounts[0])
                })
            })
        }else{
            alert("Please install Metamask")
        }
    }
    
        
    const connectWallet = () => {
            loadWeb3();
    }

    const openConnectModal = () => {
        if(isWalletConnected){

        }else{
            setShowConnectModal(true)
        }
    }




    useEffect(() => {
        
        if(localStorage.addr){
            setIsWalletConnected(true)
            setShowConnectModal(false)  
        }

        if(window.ethereum){
            window.ethereum.on("accountsChanged", accounts => {
                if (accounts.length > 0){
                  setIsWalletLoading(false)
                  setWalletAddr(accounts[0])
                  setIsWalletConnected(true)
                  setShowConnectModal(false)
                }else{
                  setIsWalletLoading(false)
                  setWalletAddr('')
                  setIsWalletConnected(false)
                  localStorage.removeItem("addr");
                  setShowConnectModal(false)
                }
              });
        }
    }, []);



    return (
        <React.Fragment>
    <div className="pageHeaderHome">
                <div className="pageHeaderWrapper">
                    <div className="headerLogoWrapper" onClick={() => history.push('/')}>
                    <div className="pageHeaderLogo">
                    
                    </div>

                    <div className="pageHeaderText2">
                  
                    </div>
                    </div>

                    <div className="headerButtonWrapper">
                    
                   

                    <div  onClick={() => openConnectModal()}  className="pageHeaderButton3">
                        <div  className="pageHeaderButtonInner3">
                            
                        {isWalletConnected ? `${walletAddr.substring(0,6)}...${walletAddr.substring(38,42)}` : isWalletLoading ? <Loader /> : "Connect Wallet" }

                        </div>
                    </div>

                    <div className="pageHeaderButton4 collectionButton">
                        <div  className="pageHeaderButtonInner4 " onClick={() => history.push('/collection')}>
                            
                            My Collection

                        </div>
                    </div>
                    </div>

                </div>
            </div>



         {showConnectModal ?  <div className="connectOverlay">
                <div className="connectWindow">
                    <div className="connectWindowHeader">
                        Connect Wallet
                    </div>
                    <div className="connectWindowClose" onClick={() => setShowConnectModal(false)}>
                    <i class="far fa-times-circle"></i>
                    </div>
                    <div className="connectSpacer"></div>
                    <div className="connectWindowItem" onClick={connectWallet}>
                        <div className="connectWindowItemIcon">
                        
                        </div>
                        <div className="connectWindowItemTitle">
                            MetaMask
                        </div>
                    </div>
                    
                </div>
                </div>
    
                    :
                    null
                    }
                   </React.Fragment>
    )
}
